<template>
	<div id="publish-stick">
		<public-header title="发布置顶"></public-header>
		<div class="content">
			<van-cell-group>
				<van-field
					v-model="name"
					label="信息置顶站点："
					type="text"
					placeholder="请输入信息置顶站点"
					input-align="right"
				/>
				<van-field
					v-model="name"
					label="信息置顶分类："
					type="text"
					placeholder="请输入信息置顶分类"
					input-align="right"
				/>
			</van-cell-group>
			<van-radio-group v-model="name" class="margin-10-t">
				<van-cell-group>
					<van-cell title="选择套餐"/>
					<van-cell title="置顶1天(0.3元)" clickable @click="name = '1'" class="border-0px">
						<van-radio name="1" />
					</van-cell>
					<van-cell title="置顶1天(0.3元)" clickable @click="name = '0'" class="border-0px">
						<van-radio name="0" />
					</van-cell>
					<van-cell title="置顶1天(0.3元)" clickable @click="name = '2'" class="no-fix border-0px">
						<van-radio name="2" />
					</van-cell>
					<van-cell title="输入其他置顶天数" clickable @click="name = '3'" class="no-fix border-0px">
						<van-radio name="3" />
					</van-cell>
					<template v-if="name == '3'">
						<van-field
							v-model="name"
							label="其他置顶天数"
							type="text"
							placeholder="请输入其他置顶天数"
							input-align="right"
						/>
					</template>
				</van-cell-group>
			</van-radio-group>
			<div class="stick-text">
				点击置顶系统会自动把推广信息置顶到所在分类的推广位置，支付后实时生效。
			</div>
			<div class="padding-15 stick-button">
				<van-button size="normal" block class="bg-danger font-16">立即置顶</van-button>
			</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		components: {
			PublicHeader,
		},
		data() {
			return {
				name: '1'
			}
		},
		methods: {

		},
		mounted() {

		}
	}
</script>

<style>
	#publish-stick .van-cell__title{
		max-width: 120px;
	}
	#publish-stick .van-icon-check{
		color: #fff;
		border: 1px solid #ccc;
		border-radius: 50px;
		margin-bottom: 1px;
		width: 20px;
		height: 20px;
		font-size: 14px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#publish-stick .van-icon-checked{
		width: 20px;
		height: 20px;
		background-color: #ff2d4b;
		color: #fff;
		border-radius: 50px;
		font-size: 14px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#publish-stick .stick-text{
		padding: 10px 15px;
		color: #999;
		font-size: 14px;
		line-height: 1.2;
	}
	#publish-stick .stick-button{
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
	}
</style>


